import { BasePage } from './../base/base';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { AppServiceProvider } from '../../providers/app-service/app-service';

/**
 * Generated class for the StatisticsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-statistics',
  templateUrl: 'statistics.html',
})
export class StatisticsPage extends BasePage {

  role: any;
  airMonitors: boolean;
  contaminateds: boolean;
  automatics: boolean;
  statistics: boolean;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCotrl: ToastController) {
    super(navCtrl, navParams, toastCotrl)
    this.role = AppServiceProvider.getInstance().userInfo.role[0];
    if (this.role == '1' || this.role == '77') {
      this.airMonitors = true;
      this.contaminateds = true;
      this.automatics = true;
      this.statistics = true;
    } else if (this.role == '78') {
      this.airMonitors = false;
      this.contaminateds = false;
      this.automatics = true;
      this.statistics = false;
    }else if (this.role == '79') {
      this.airMonitors = false;
      this.contaminateds = true;
      this.automatics = false;
      this.statistics = false;
    }else if (this.role == '81') {
      this.airMonitors = true;
      this.contaminateds = false;
      this.automatics = false;
      this.statistics = false;
    }else if (this.role == '82') {
      this.airMonitors = false;
      this.contaminateds = false;
      this.automatics = false;
      this.statistics = true;
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad StatisticsPage');
  }
  airMonitor() {
    this.navCtrl.push("AirMonitorPage");
  }
  contaminated() {
    this.navCtrl.push("ContaminatedMonitorPage");
  }
  automatic() {
    this.navCtrl.push("AutomaticMonitorPage")
  }
  handle() {
    this.navCtrl.push("HandleMonitorPage")
  }
}



